<template >
	<div id="evaluate">
		<c-title :hide="false"
		         text='评价'></c-title>
		<div style="height: 42px;"></div>
		<div id="shop" v-for="good in order.has_many_order_goods">
			<div class="goods">
				<div class="img"><img v-lazy="good.thumb"></div>
				<div class="inner">
					<div class="name">{{good.title}}</div>
					<div class="option">规格: {{good.goods_option_title}}</div>
				</div>
				<div class="price">
					￥{{good.goods_price}}
					<br/> ×{{good.total}}
				</div>
			</div>
	
			<div id="pjall">
				<div id="pj"><span>评分:</span>
					<el-rate v-model="level"
					         show-text
					         @change="getStar"></el-rate>
				</div>
				<div style="height:10px"></div>
				<el-input type="textarea"
				          :rows="2"
				          placeholder="请输入评论内容"
				          v-model="comment">
				</el-input>
			</div>
			<el-button type="primary"
			           @click="toComment(good)">提交评价</el-button>
		</div>
	
	</div>
</template>
<script>
import order_list_evaluatecontroller from './order_list_evaluatecontroller';
export default order_list_evaluatecontroller;
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
#evaluate {
	#shop {
		background: #FFF;
		padding: 10px;
	}
	.goods {
		display: flex;
		align-items: stretch;
		flex-flow: row wrap;
		background: #efeded;
		padding: 5px;
		margin-bottom: 5px;
		.img {
			flex: 3;
			img {
				width: 100%;
			}
		}
		.inner {
			flex: 5;
			padding: 0 5px;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}
		.name {
			flex: 5;
			text-align: left
		}
		.price {
			flex: 2;
			align-items: center;
		}
		.option {
			color: #888;
			font-size: .6rem;
			flex: 1;
		}
	}
	#pjall {
		background: #FFF;
		padding: 15px;
		textarea {
			width: 87%;
			margin-top: 10px;
			padding: 10px;
		}
		#pj {
			text-align: left;
			span {
				float: left;
				font-size: 14px;
				padding-right: 10px;
			}
		}
	}
	button {
		width: 100%;
	}
}
</style>


